"use client";

import styled from "styled-components";
import MySubscribeNav from "@/components/mypage/tabpage/MySubscribeNav";
import MySubscribeContents from "@/components/mypage/tabpage/MySubscribeContents";
import { useEffect, useState } from "react";
import NoContentsPage from "@/components/mypage/common(temporary)/NoContentsPage";
import { USER1 } from "@/constants/mypageData";
import { IMySummary } from "@/models/newsDetail";

function MySubscribe() {
  const [activeCategory, setActiveCategory] = useState<string>("");
  const [summaryInfo, setSummaryInfo] = useState<IMySummary[] | null>(null);

  // Îç∞Ïù¥ÌÑ∞ Fetch
  useEffect(() => {
    async function fetchSummary() {
      try {
        const res = await fetch(`http://localhost:3001/mysummary`);
        if (!res.ok) {
          throw new Error("No such article found");
        }
        const data = await res.json();
        setSummaryInfo(data);
      } catch (err) {
        console.error(err);
      }
    }
    fetchSummary();
  }, []);

  // 2) IntersectionObserverÎ°ú ÌòÑÏû¨ ÌôîÎ©¥Ïóê ÎÇòÌÉÄÎÇú ÏÑπÏÖò Í∞êÏßÄ
  useEffect(() => {
    if (!summaryInfo) return;

    const sections = document.querySelectorAll<HTMLElement>(".my-subs-content");
    const visibleSet = new Set<HTMLElement>();
    // ÌòÑÏû¨ ÌôîÎ©¥Ïóê Îì§Ïñ¥ÏôÄ ÏûàÎäî ÏÑπÏÖòÎì§ÏùÑ Ï†ÄÏû•Ìï¥Îëò Set

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (entry.isIntersecting) {
          // ÌôîÎ©¥Ïóê Î≥¥Ïù¥Îäî ÏÑπÏÖòÏù¥Î©¥ SetÏóê Ï∂îÍ∞Ä
          visibleSet.add(entry.target as HTMLElement);
        } else {
          // Î∑∞Ìè¨Ìä∏ÏóêÏÑú Î≤óÏñ¥ÎÇòÎ©¥ SetÏóêÏÑú Ï†úÍ±∞
          visibleSet.delete(entry.target as HTMLElement);
        }
      });

      // Ïù¥Ï†ú visibleSetÏóêÎäî "ÌòÑÏû¨ Î≥¥Ïù¥Îäî ÏÑπÏÖòÎì§"Ïù¥ Îì§Ïñ¥ ÏûàÏùå
      if (visibleSet.size > 0) {
        // arrayÎ°ú Î≥ÄÌôò ÌõÑ, Í∞ÄÏû• ÏúÑÏóê ÏûàÎäî ÏàúÏÑúÎåÄÎ°ú Ï†ïÎ†¨
        const sorted = Array.from(visibleSet).sort((a, b) => {
          return a.getBoundingClientRect().top - b.getBoundingClientRect().top;
        });
        // "Í∞ÄÏû• ÏúÑÏóê ÏûàÎäî" ÏÑπÏÖòÏùò idÎ°ú activeCategory ÏÑ§Ï†ï
        setActiveCategory(sorted[0].id);
      }
    }, {
      threshold: 0.3,  // ÏñºÎßàÎÇò Î≥¥Ïó¨Ïïº isIntersecting Ï≤òÎ¶¨Ìï†ÏßÄ
      rootMargin: "0px 0px -10% 0px",
      /*
        ÏïÑÎûòÏ™ΩÏóê ÏÇ¥Ïßù Ïó¨Ïú†Î•º ÎëêÎ©¥,
        ÏïÑÏßÅ ÏôÑÏ†ÑÌûà Î≤óÏñ¥ÎÇòÏßÄ ÏïäÏïòÏñ¥ÎèÑ Îπ†ÏßÄÎäî Í≤ÉÏúºÎ°ú Ï≤òÎ¶¨Ìï† Ïàò ÏûàÏùå.
        (Î†àÏù¥ÏïÑÏõÉÏóê ÎßûÏ∂∞ Ï°∞Ï†ï)
      */
    });

    sections.forEach((section) => observer.observe(section));

    return () => {
      sections.forEach((section) => observer.unobserve(section));
    };
  }, [summaryInfo]);


  // 3) Íµ¨ÎèÖÏ§ëÏù¥Î©¥ ÎÑ§ÎπÑ + Ïª®ÌÖêÏ∏† ÌëúÏãú, ÏïÑÎãàÎ©¥ NoContentsPage ÌëúÏãú
  return (
    <>
      {(USER1.subscribe === 1 || USER1.subscribe === 2) ? (
        <>
          <MySubscribeNav activeCategory={activeCategory} />
          <MySubscribeStyled>
            <MySubscribeContents summaryInfo={summaryInfo} />
          </MySubscribeStyled>
        </>
      ) : (
        <NoContentsPage
          text={`Îâ¥Ïä§Î†àÌÑ∞Î•º Íµ¨ÎèÖ Ï§ëÏù¥ ÏïÑÎãôÎãàÎã§. \n ÏÉàÎ°úÏö¥ Îâ¥Ïä§Î†àÌÑ∞ Íµ¨ÎèÖÏùÑ ÏãúÏûëÌï¥Î≥¥ÏãúÍ≤†ÏäµÎãàÍπå?`}
          btnText={"Íµ¨ÎèÖ Î∞îÎ°úÍ∞ÄÍ∏∞"}
          moveTo={"/"}
        />
      )}
    </>
  );
}

const MySubscribeStyled = styled.div`
    position: relative;
    max-width: 1024px;
    margin: 0 auto;
`;

export default MySubscribe;
